<template>
  <div class="m-home">
    <div class="swipe-bg primary" :class="{ blue: !skeleton }"></div>

    <van-sticky>
      <header class="header">
        <Search :is-slot="true" :readonly="true" :background="skeleton ? '#ff6034' : 'rgba(30, 144, 255, 1)'" :placeholder="'搜索'" :autofocus="true" @click.native="$router.push('/m/search')">
          <div class="btn-list primary" :class="{ blue: !skeleton }">
            <div class="btn" @click.stop="getTip"><i class="van-icon van-icon-bullhorn-o"></i></div>
            <div class="btn" @click.stop="getHelp"><i class="van-icon van-icon-service-o"></i></div>
          </div>
        </Search>
      </header>
    </van-sticky>

    <main class="main-box">
      <div class="swiper-box">
        <div style="height: 14rem" v-if="skeleton"></div>
        <Swiper class="animate__animated animate__fadeIn" :swiper="swiper" v-else />
      </div>

      <div class="mHome-loading" v-if="skeleton">
        <el-skeleton animated>
          <template slot="template">
            <div style="margin: 0 1rem;">
              <div style="display: flex; flex-wrap: wrap; justify-content: space-between; margin-bottom: 1rem;">
                <div style="width: 43.5%; border-radius: 1rem; overflow: hidden; background-color: white; padding: .8rem; display: flex; flex-direction: column; margin-bottom: 1rem;" v-for="item in 6" :key="item">
                  <el-skeleton-item variant="image" style="width: 100%; height: 17.2rem;"></el-skeleton-item>
                  <el-skeleton-item variant="text" style="width: 80%; height: 1rem; margin: 1rem 0;"></el-skeleton-item>
                  <el-skeleton-item variant="text" style="width: 30%; height: 1rem;"></el-skeleton-item>
                </div>
              </div>
            </div>
          </template>
        </el-skeleton>
      </div>
      <GoodsList class="animate__animated animate__fadeIn" :list="mHomeList" v-else />
    </main>

    <Footer />

    <van-popup v-model="showTopTip" position="top" duration="0.2" :style="[{ height: '60px' }, { background: '#07C160' }]">
      <div class="tip-content">
        <div class="item-tip">
          <span>客服QQ: 410021214</span>
          <span>客服微信: ios173aa</span>
        </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
import Vue from 'vue'
import { mapState, mapActions } from 'vuex'
import { Lazyload } from 'vant'

import Search from "@/views/mobile/components/Search"
import GoodsList from "@/views/mobile/components/GoodsList"
import Footer from "@/views/mobile/components/Footer"
import Swiper from "@/views/mobile/components/Swiper"

Vue.use(Lazyload)

export default {
  name: "MHome",
  computed: {
    ...mapState(['goodsList']),
  },
  data () {
    return {
      skeleton: false, // 骨架屏加载
      timer: null,     // 定时器
      swiper: {
        data: [],      // 轮播列表
        config: {},    // 轮播配置
      },
      mHomeList: [],   // 首页商品列表
      showTopTip: false, // 顶部公告提示
    }
  },

  methods: {
    ...mapActions(['getGoodsList']),

    // 顶部公告
    getTip () {
      this.showTopTip = true
      clearTimeout(this.timer)

      // 3s 后自动关闭
      this.timer = setTimeout(() => {
        this.showTopTip = false
      }, 5000)
    },

    // 帮助中心
    getHelp () {
      this.$dialog.alert({
        message: '帮助中心'
      })
    },

    // 获取轮播列表
    getSwiperList () {
      let res = [
        { id: 1, image: 'https://img01.yzcdn.cn/vant/apple-1.jpg', to: 'detail/1' },
        { id: 2, image: 'https://img01.yzcdn.cn/vant/apple-2.jpg', to: 'detail/2' },
      ]

      this.swiper.data = res
      this.swiper.config = { width: '100%', height: '140px', }
    },

    // 获取首页数据
    getMHomeList () {
      this.skeleton = true
      this.timer = setTimeout(() => {
        this.getGoodsList()
        this.goodsList.map(i => {
          this.mHomeList.push(i)
        })
        this.skeleton = false
      }, 500)
    },

  },

  components: {
    Search,
    Swiper,
    GoodsList,
    Footer,
  },

  mounted () {
    // 初始化轮播列表
    this.getSwiperList()
    // 初始化商品列表
    this.getMHomeList()
  },

  destroyed () {
    clearTimeout(this.timer)
  }
}
</script>

<style scoped lang="stylus">
.m-home
  position relative
  margin-bottom 5rem

  .tip-content
    color white
    height 100%
    display flex
    justify-content center
    align-items center

    .item-tip
      display flex
      flex-direction column
      line-height 1.4

  .swipe-bg
    position absolute
    top 0
    left -25%
    width 150%
    height 14rem
    border-bottom-left-radius 100%
    border-bottom-right-radius 100%

    &.primary
      background-color #FF6034
    &.blue
      background-color rgba(30, 144, 255, 1)

  .header
    position relative
    display flex
    align-items center

    &.primary
      background-color #FF6034
    &.blue
      background-color rgba(30, 144, 255, 1)


    .van-search
      display inline-block

    .btn-list
      height 5.4rem
      display flex
      align-items center
      background-color transparent

      &.primary
        background-color #FF6034
      &.blue
        background-color #1e90ff

      .btn
        display inline-block
        color white
        font-size 2.5rem

        i
          margin 0 .5rem

  .main-box
    position relative

    .swiper-box
      margin 0 1rem


</style>