<template>
  <div>
    <div class="side-bar">
      <div class="item" @click="$router.push('/query')">
        <i class="iconfont icon-search2"></i>订单查询
      </div>

      <div class="item" @click="WebsiteAnnouncement">
        <i class="iconfont icon-notice1"></i>网站公告
      </div>

      <div class="item" @click="contactKefu">
        <i class="iconfont icon-wodekefu"></i>联系客服
      </div>

      <div class="item" v-if="showToTop" @click="goToTop">
        <i class="iconfont icon-fanhuidingbu"></i>返回顶部
      </div>
    </div>

    <el-dialog :title="announce.title" :visible.sync="announce.visible" :show-close="false" width="30%">
      <span>{{ announce.content }}</span>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="announce.visible = false">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: "SideBar",
  data () {
    return {
      timer: null,
      showToTop: false, // 是否显示回到顶部
      scrollTop: 0,
      announce: {},
    }
  },

  computed: {
    ...mapState(['contactConfig']),
  },

  methods: {
    // 网站公告
    WebsiteAnnouncement () {
      this.announce = {
        title: '网站公告',
        visible: true,
        content: '客服QQ: 410021214 客服微信: ios173aa',
      }
    },

    // 联系客服
    contactKefu () {
      this.$alert(this.contactConfig.img, this.contactConfig.title, this.contactConfig.config)
    },

    // 滚动显示/隐藏回到顶部按钮
    scroll () {
      window.onscroll = () => {
        this.scrollTop = document.documentElement.scrollTop
        this.showToTop = this.scrollTop >= 200 ? true : false
      }
    },

    // 回到顶部
    goToTop () {
      this.timer = setInterval(() => {
        this.scrollTop -= 20
        document.documentElement.scrollTop = this.scrollTop

        if (this.scrollTop <= 0) {
          clearInterval(this.timer)
        }
      }, 10)
    },
  },

  mounted() {
    this.scroll()
  },

  destroyed() {
    clearInterval(this.timer)
  }
}
</script>

<style scoped lang="stylus">
.side-bar
  position fixed
  right 3rem
  bottom 6.5rem
  z-index 999

  .item
    width 6rem
    height 6rem
    margin-bottom 1rem
    border-radius 5px
    background-color rgba(0,0,0,.4)
    color white
    display flex
    flex-direction column
    justify-content center
    align-items center
    font-size 1.2rem
    cursor pointer

    i
      font-size 2.5rem
      margin-bottom .5rem
</style>