<template>
  <div class="m-detail">
    <div class="detail-content">
      <Swiper :swiper="swiper" />

      <div class="back-home" @click="$router.push('/m')">
        <i class="van-icon van-icon-wap-home-o"></i>
      </div>

      <div class="content-item">
        <div class="title-price">
          <div class="title">
            <van-tag class="rec" type="danger" v-if="info.isRecommend">推荐</van-tag> {{ info.title }}
          </div>

          <div class="price"><span class="sign">￥</span>{{ info.sku?.price | twoDecimal }} <span class="start">起</span></div>
        </div>
      </div>

      <div class="content-item page-parameter">
        <div class="spec-box">
          <label>选择</label>

          <div class="spec">
            <span class="list" @click="showSpecBox">共有5种规格可选</span>
            <span class="sold">已售 {{ info.sold }}</span>
          </div>
        </div>

        <div class="service">
          <label>服务</label>

          <div class="list">
            <van-tag type="primary" v-for="(item, index) in info.service" :key="index">{{ item.name }}</van-tag>
          </div>
        </div>
      </div>

      <div class="content-item detail-info">
        <div class="head">
          <i data-v-ce67cd30="" class="iconfont icon-sijiaoxing"></i>
          详情介绍
          <i data-v-ce67cd30="" class="iconfont icon-sijiaoxing"></i>
        </div>

        <div class="detail-box">
          {{ info.content }}
        </div>
      </div>

      <van-goods-action>
        <van-goods-action-icon icon="chat-o" color="rgba(238, 10, 36, 1)" text="客服" @click="contactKefu" />
        <van-goods-action-icon icon="logistics" color="rgba(238, 10, 36, 1)" text="查单" @click="$router.push('/m/query')" />
        <van-goods-action-button type="danger" text="立即购买" @click="showSpecBox" />
      </van-goods-action>
    </div>

    <!-- 规格 -->
    <van-sku
      ref="vanSku"
      v-model="showSpec"
      :sku="sku"
      :goods="goods"
      :goods-id="goodsId"
      :quota="quota"
      :quota-used="quotaUsed"
      :initial-sku="initialSku"
      :show-add-cart-btn="false"
      :start-sale-num="1"
      :customSkuValidator="customSkuValidator"
      @buy-clicked="toPay"
    />

    <!-- 联系客服 -->
    <Contact :show-kefu="showKefu" :close-contact="closeContact" />
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import Swiper from "@/views/mobile/components/Swiper"
import Contact from "@/views/mobile/components/Contact"

export default {
  name: "MDetail",
  components: {
    Swiper,
    Contact,
  },

  computed: {
    ...mapState(['mobileDetailPageData']),

    // 详情页商品 id
    id () {
      return this.$route.params.id
    }
  },

  data () {
    return {
      showKefu: false,
      swiper: {
        data: [],      // 轮播列表
        config: {},    // 轮播配置
      },

      // 规格
      showSpec: false,
      info: {}, // 商品信息
      sku: {},
      goods: {},
      goodsId: 0,
      quota: 0,
      quotaUsed: 0,
      initialSku: {},
      customSkuValidator: Function,
    }
  },

  methods: {
    ...mapActions(['getMobileDetailPageData']),

    // 联系客服
    contactKefu () {
      this.showKefu = true
    },

    // 关闭联系客服
    closeContact () {
      this.showKefu = false
    },

    // 获取商品信息
    getGoodData () {
      this.getMobileDetailPageData()
      let res = this.mobileDetailPageData

      // 商品信息
      this.info = res
      this.sku = res.sku
      this.goods = res.goods
      this.goodsId = res.goods_id
      this.quota = res.quota
      this.quotaUsed = res.quotaUsed
      this.initialSku = res.initialSku
      this.customSkuValidator = res.customSkuValidator

      // 轮播图
      this.swiper.data = res.carousel
      this.swiper.config = { width: '100%', height: '35rem', }
    },

    // 立即购买 - 打开规格选项
    showSpecBox () {
      this.showSpec = true
    },

    // 立即购买 - 去收银台页面
    toPay () {
      // 通过 ref 获取 Sku 实例并调用实例方法获取当前 SkuData
      let info = JSON.stringify(this.$refs.vanSku.getSkuData())

      // 将当前 SkuData 传给收银台
      this.$router.push({
        path: '/m/pay',
        query: { info }
      })
    },

  },

  mounted() {
    this.getGoodData()
  }

}
</script>

<style scoped lang="stylus">
.page-swiper
  border-radius 0

.m-detail
  background-color #f5f6f7
  min-height 100vh
  overflow hidden

  .detail-content
    position relative

    .back-home
      position absolute
      top 1rem
      left 1rem
      z-index 2
      display flex
      justify-content center
      align-items center
      background-color rgba(0,0,0,.4)
      border-radius 100%
      width 4rem
      height 4rem

      .van-icon
        color white
        font-size 3rem
        display block

    .content-item
      background-color white

      .title-price
        display flex
        justify-content space-between
        align-items flex-start
        padding 1rem

        .title
          font-size 1.6rem
          font-weight bold
          width 70%

          .rec
            border-radius 999px
            margin-right .5rem

        .price
          flex 1
          color red
          font-size 2.8rem

          .sign, .start
            font-size 1.2rem

      &.page-parameter
        margin 1rem 0
        padding .8rem 1rem

        label
          color #888
          margin-right 1rem

        .spec-box
          display flex
          color #5a95e3

          .spec
            flex 1
            display flex
            justify-content space-between

            .list
              background-color #f2f2f2
              color #999
              border-radius 5px
              padding .3rem 1rem

            .sold
              margin-left auto
              color #999

        .service
          display flex
          margin-top 1rem

          .van-tag
            margin-right .5rem

            &:first-child
              background-color rgba(81, 155, 224, 1)

            &:nth-child(2)
              background-color rgba(107, 224, 81, 1)

            &:nth-child(3)
              background-color rgba(81, 224, 180, 1)

      &.detail-info
        .head
          font-size 1.6rem
          text-align center
          height 4rem
          line-height 4rem

          i
            font-size 1.2rem

        .detail-box
          background-color white
          padding 0 1rem
          min-height 40rem
          margin 1rem 0 5rem 0

</style>