<template>
  <div class="m-search">
    <Search :search-value.sync="searchValue" :autofocus="true" :placeholder="'输入名称搜索'" :do-search="searchGoods" :right-text="'搜索'" ref="search" />

    <div class="empty" v-if="showGoodsList && searchList.length == 0">
      <el-empty :image-size="100" description="没有相关商品哦"></el-empty>
    </div>

    <div class="recommend-list" v-if="!showGoodsList">
      <div class="title">限时推荐</div>

      <div class="list">
        <div class="item" v-for="(item, index) in recommendList" :key="index" @click="doSearchByRec(item.title)">{{ item.title }}</div>
      </div>
    </div>

    <GoodsList :list="searchList" :not-more="false" v-show="showGoodsList" />
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import GoodsList from "@/views/mobile/components/GoodsList"
import Search from "@/views/mobile/components/Search"

export default {
  name: "MSearch",
  components: {
    GoodsList,
    Search,
  },
  computed: {
    ...mapState(['goodsList']),
  },
  data () {
    return {
      searchValue: '', // 搜索内容
      showGoodsList: false, // 是否显示搜索列表
      recommendList: [], // 推荐列表
      searchList: [], // 搜索的商品列表
    }
  },

  methods: {
    ...mapActions(['getGoodsList']),

    // 获取推荐列表
    getRecommendList () {
      let res = [
        { id: 1, title: '美国-Apple iTunes 苹果礼品卡' },
        { id: 2, title: '苹果欧盟各国小面值' },
        { id: 3, title: '澳大利亚-Apple苹果卡' },
        { id: 4, title: '加拿大-Apple苹果卡' },
        { id: 5, title: '美国-Google Play 谷歌礼品卡' },
        { id: 6, title: '英国-Apple iTunes 苹果礼品卡' },
        { id: 7, title: '美国-Steam 充值卡' },
        { id: 8, title: '美国-Amazon 亚马逊礼品卡' },
        { id: 9, title: '土耳其-Apple 苹果卡' },
      ]

      this.recommendList = res
    },

    // 获取搜索商品列表
    searchGoods () {
      if (!this.searchValue) {
        this.getGoodsList()
        this.showGoodsList = true
        this.searchList = this.goodsList
        return
      }

      this.getGoodsList()
      this.showGoodsList = true
      this.searchList = this.goodsList.filter(i => i.title.includes(this.searchValue))

      // 自动聚焦
      this.$refs.search.$el.querySelector('input').focus()
    },

    // 清除搜索框
    clearValue () {
      this.searchValue = ''
    },

    // 点击 限时推荐 搜索
    doSearchByRec (title) {
      this.showGoodsList = true
      this.searchValue = title
      this.searchGoods()
    },

  },

  mounted() {
    this.getRecommendList()
  }
}
</script>

<style scoped lang="stylus">
.m-search
  .recommend-list
    padding 1.5rem
    padding-top .5rem
    background-color white

    .title
      margin-bottom 1rem

    .list
      display flex
      flex-wrap wrap

      .item
        color #686868
        font-size 1.2rem
        background-color #f0f2f5
        margin 0 1rem 1rem 0
        padding 0 1.3rem 0 1.3rem
        border-radius 3px
        height 2.4rem
        line-height 2.4rem

</style>