<template>
  <div class="query-view">
    <div class="main">
      <div class="main-box">
        <div class="search">
          <el-input v-model="queryText" placeholder="输入手机号或订单号" @keyup.enter.native="getQueryList(queryText)"></el-input>
          <el-button type="primary" @click="getQueryList(queryText)">查询</el-button>
        </div>

        <div class="content">
          <div class="list-left">
            <div class="title">
              <i class="b"></i>
              <span class="reLook">看了又看</span>
            </div>

            <div class="list-l">
              <a class="list-a" href="javascript:;" v-for="(item, index) in lookList" :key="index" @click="toDetailPage(item.id)">
                <img class="animate__animated animate__fadeIn" :src="item.thumbnail" alt="">
                <span class="price">￥ {{ item.spec.filter(i => i.active)[0].price }}</span>
              </a>
            </div>
          </div>

          <div class="list-right">
            <div class="head">订单详细</div>

            <loadingComp :loading="loading" v-if="loading"></loadingComp>

            <div class="empty" v-if="showEmpty">
              <el-empty :image-size="100" description="暂无相关订单"></el-empty>
            </div>

            <div class="list-content">
              <ul>
                <li v-for="(item, index) in queryList" :key="index">
                  <div class="animate__animated animate__fadeIn list-detail">
                    <img class="thumbnail" :src="item.thumbnail" alt="" @click="toDetailPage(item.id)">

                    <div class="info">
                      <span class="title" @click="toDetailPage(item.id)">{{ item.title }}</span>
                      <span class="price" @click="toDetailPage(item.id)">{{ item.spec.filter(i => i.active)[0].value }}</span>
                      <span class="time">{{ item.createdAt }}</span>
                      <div class="btn-list">
                        <el-button type="success" round size="small" @click="lookPwd(item.cardPwd)">查看卡密</el-button>
                        <el-button type="success" round size="small" @click="copyPwd(item.cardPwd)">复制卡密</el-button>
                        <el-button type="primary" round size="small" @click="storeRemarks(item.remarks)">商家备注</el-button>
                      </div>
                    </div>

                    <div class="sumUp">
                      <span class="price">￥ {{ item.spec.filter(i => i.active)[0].price }}</span>
                      <span class="num">&times; {{ item.count }}</span>
                      <span class="mone">总价: ￥ {{ item.spec.filter(i => i.active)[0].price * item.count }}</span>
                    </div>
                  </div>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>

    <el-dialog :title="dialog.title" :visible.sync="dialog.dialogVisible" :show-close="dialog.showClose" width="30%">
      <span>{{ dialog.content }}</span>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialog.dialogVisible = false" v-if="dialog.confirmVisible">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'

export default {
  name: "QueryView",
  data () {
    return {
      loading: false,   // 加载
      timer: null,      // 定时器
      showEmpty: false, // 数据为空
      queryText: '',    // 搜索内容
      lookList: [],     // 看了又看列表
      queryList: [],    // 搜索列表
      // 提示框
      dialog: {
        dialogVisible: false,
        showClose: true,
        title: '',
        content: '',
        confirmVisible: true,
      },
    }
  },

  computed: {
    ...mapState(['goodsList'])
  },

  methods: {
    ...mapActions(['getGoodsList']),

    // 访问详情页
    toDetailPage (id) {
      this.$router.push(`/detail/${ id }`)
    },

    // 看了又看列表
    getLookList () {
      this.loading = true

      this.timer = setTimeout(() => {
        this.getGoodsList()
        this.lookList = this.goodsList.slice(2, 6)
        this.loading = false
      }, 500)
    },

    // 查询列表
    getQueryList (queryText) {
      this.showEmpty = true

      // 查询关键词为空时提示
      if (!queryText) {
        this.queryList = [] // 为空时搜索列表也为空

        this.$message({
          type: 'info',
          message: '请输入手机号或订单号'
        })
        return
      }

      // 通过 queryText 搜索相应的订单
      this.loading = true
      this.timer = setTimeout(() => {
        this.queryList = this.goodsList

        // 关闭 loading
        this.loading = false
        // 关闭空数据状态
        this.showEmpty = false
      }, 500)
    },

    // 查看卡密
    lookPwd (pwd) {
      this.dialog = {
        dialogVisible: true,
        title: '订单内容',
        content: pwd,
        confirmVisible: false,
      }
    },

    // 复制到剪贴板
    fallbackCopyTextToClipboard(text) {
      // 1.创建一个可选中元素
      let textArea = document.createElement("textarea")
      textArea.value = text

      // 2.使用定位，阻止页面滚动
      textArea.style.top = "0"
      textArea.style.left = "0"
      textArea.style.position = "fixed"
      document.body.appendChild(textArea)
      textArea.focus()
      textArea.select()

      try {
        var successful = document.execCommand('copy')
        var msg = successful ? 'successful' : 'unsuccessful'
        console.log('Fallback: Copying text command was ' + msg)
      } catch (err) {
        console.error('Fallback: Oops, unable to copy', err)
      }

      // 3.移除元素
      document.body.removeChild(textArea)
    },

    // 复制卡密
    copyPwd(pwd) {
      if (!navigator.clipboard) {
        this.fallbackCopyTextToClipboard(pwd)
        return
      }

      navigator.clipboard.writeText(pwd)
        .then(() => {
          // console.log('Async: Copying to clipboard was successful!')

          this.$message({
            type: 'success',
            message: '复制成功！'
          })
        }, (err) => {
          console.error('Async: Could not copy text: ', err)

          this.$message({
            type: 'error',
            message: '复制功能有问题，请向管理员反馈！'
          })
        })
    },

    // 商家备注
    storeRemarks (remarks) {
      this.dialog = {
        dialogVisible: true,
        showClose: false,
        title: '商家备注',
        content: remarks,
        confirmVisible: true,
      }
    },
  },

  mounted() {
    this.showEmpty = true
    this.getLookList()
  },

  destroyed() {
    clearTimeout(this.timer)
  }
}
</script>

<style scoped lang="stylus">
.main-box
  background-color white
  padding 2rem
  display flex
  flex-direction column
  align-items center

  .search
    display flex
    margin 3rem 0 5rem

    .el-input
      width 50rem
      height 2rem

      /deep/ .el-input__inner
        border-radius 5px 0 0 5px!important

    .el-button
      border-radius 0 3px 3px 0

  .content
    display flex
    justify-content space-between
    width 100%

    .list-left
      width 17rem
      padding 0 2rem 0 1rem
      border-right 1px solid #f2f2f2
      margin-right 2rem

      .title
        position relative
        width 100%
        height 2.5rem

        .b
          border-top 1px dotted #c9c9c9
          position absolute
          top 1rem
          left 0
          width 100%

        .reLook
          color #999
          background-color white
          position absolute
          top 0
          left calc(50% - 3.8rem)
          padding 0 1rem

      .list-l
        .list-a
          display block
          position relative
          margin-bottom 1rem

          img
            width 100%
            height 100%
            object-fit cover

          .price
            position absolute
            left 0
            bottom 0
            background rgba(214,206,206,.8)
            color #515a6e
            width 100%
            height 3rem
            display flex
            justify-content center
            align-items center
            font-family Arial

    .list-right
      width 100%
      border 1px solid #ececec
      margin 0 1rem 0 3rem

      .head
        height 4rem
        line-height 4rem
        background-color #f1f1f1
        font-size 1.6rem
        padding-left 2rem

      .list-content ul li
        padding 2rem
        border-bottom 1px solid #f1f1f1

        .list-detail
          display flex

          .thumbnail
            width 12.5rem
            height 12.5rem
            object-fit cover
            cursor pointer

          .info
            margin-left 2rem
            display flex
            flex-direction column
            font-size 1.6rem
            width 70%

            .title
              color black
              font-weight bold
              cursor pointer
              display -webkit-box
              -webkit-box-orient vertical
              -webkit-line-clamp 1
              overflow hidden

            .price
              margin 1rem 0
              cursor pointer

            .time
              margin 0 0 1rem

            .btn-list
              button
                font-size 1.4rem

              .el-button--success
                background-color #19BE6B

              .el-button--primary
                background-color #2D8CF0


          .sumUp
            flex 1
            margin-left auto
            display flex
            flex-direction column
            align-items flex-end
            font-size 1.6rem
            color black

            .num
              margin 1rem 0

</style>