<template>
  <div class="m-query">
    <Search :search-value.sync="searchValue" :placeholder="'请输入手机号/商户订单号'" :autofocus="true" :do-search="searchGoods" :right-text="'查询'" />

    <div class="empty" v-if="queryList.length == 0">
      <el-empty :image-size="100" description="暂无记录"></el-empty>
    </div>

    <div class="query-list" v-else>
      <div class="list" v-for="(item, index) in goodsList" :key="index">
        <div class="head">
          <span class="icon-gift"><i class="van-icon van-icon-send-gift"></i> {{ item.orderNumber }}</span>
          <span>{{ item.status }}</span>
        </div>

        <div class="main">
          <div class="thumbnail" @click="toDetailPage(item.id)"><img :src="item.thumbnail" alt=""></div>

          <div class="detail">
            <div class="item title-price">
              <div class="title" @click="toDetailPage(item.id)">{{ item.title }}</div>
              <div class="price"><span class="icon-currency">￥</span> {{ item.spec.filter(i => i.active)[0].price | twoDecimal }}</div>
            </div>

            <div class="item spec-num">
              <div class="spec" @click="toDetailPage(item.id)">{{ item.spec.filter(i => i.active)[0].value }}</div>
              <div class="num">&times;{{ item.count }}</div>
            </div>

            <div class="item time-totalPrice">
              <div class="time">{{ item.createdAt }}</div>
              <div class="totalPrice">总价: <span class="icon-currency">￥</span> {{ item.spec.filter(i => i.active)[0].price * item.count | twoDecimal }}</div>
            </div>
          </div>
        </div>

        <div class="foot">
          <van-button type="info" size="small" plain round hairline class="item-btn" @click="storeRemarks(item.remarks)">商家备注</van-button>
          <van-button type="danger" size="small" plain round hairline class="item-btn" @click="lookPwd(item.cardPwd)">查看卡密</van-button>
          <van-button type="danger" size="small" plain round hairline class="item-btn" @click="copyPwd(item.cardPwd)">复制卡密</van-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import Search from "@/views/mobile/components/Search"

export default {
  name: "MQuery",
  components: {
    Search,
  },
  computed: {
    ...mapState(['goodsList']),
  },

  data () {
    return {
      searchValue: '',
      queryList: [],
    }
  },

  methods: {
    ...mapActions(['getGoodsList']),

    // 加载效果
    toastLoading (duration=500, message='加载中...') {
      this.$toast.loading({
        duration,
        forbidClick: true,
        message,
      })
    },

    // 搜索商品
    searchGoods () {
      if (!this.searchValue) {
        this.toastLoading(100)
        this.queryList = []
        return
      }

      this.toastLoading(500)
      this.getGoodsList()
      this.queryList = this.goodsList
    },

    // 商家备注
    storeRemarks (remarks) {
      this.$dialog.alert({
        message: remarks
      })
    },

    // 复制到剪贴板
    fallbackCopyTextToClipboard(text) {
      // 1.创建一个可选中元素
      let textArea = document.createElement("textarea")
      textArea.value = text

      // 2.使用定位，阻止页面滚动
      textArea.style.top = "0"
      textArea.style.left = "0"
      textArea.style.position = "fixed"
      document.body.appendChild(textArea)
      textArea.focus()
      textArea.select()

      try {
        var successful = document.execCommand('copy')
        var msg = successful ? 'successful' : 'unsuccessful'
        console.log('Fallback: Copying text command was ' + msg)
      } catch (err) {
        console.error('Fallback: Oops, unable to copy', err)
      }

      // 3.移除元素
      document.body.removeChild(textArea)
    },

    // 复制卡密
    copyPwd(pwd) {
      if (!navigator.clipboard) {
        this.fallbackCopyTextToClipboard(pwd)
        return
      }

      navigator.clipboard.writeText(pwd)
          .then(() => {
            // console.log('Async: Copying to clipboard was successful!')

            this.$toast({
              type: 'success',
              message: '复制成功！'
            })
          }, (err) => {
            console.error('Async: Could not copy text: ', err)

            this.$toast({
              type: 'error',
              message: '复制功能有问题，请向管理员反馈！'
            })
          })
    },

    // 查看卡密
    lookPwd (pwd) {
      this.$dialog.alert({
        message: pwd,
        messageAlign: 'left',
      })
    },

    // 去详情页
    toDetailPage(id) {
      this.$router.push(`/m/detail/${ id }`)
    }

  },

  destroyed () {
    this.$toast.clear()
  }

}
</script>

<style scoped lang="stylus">
.m-query
  margin-bottom 6rem

.query-list
  .list
    background-color white
    border-radius 1rem
    padding 0 1rem
    padding-bottom .5rem
    margin 1rem .8rem

    .head
      line-height 4rem
      display flex
      justify-content space-between
      color green

      .icon-gift
        color black
        font-size 1.6rem
        display flex
        align-items center

        &:before
          content none

        i
          color rgba(238, 10, 36, 1)
          font-size 1.6rem
          vertical-align middle
          margin-right .5rem

    .main
      display flex

      .thumbnail
        width 9rem
        height 9rem
        border-radius 1rem
        margin-right 1rem

        img
          width 100%
          height 100%
          object-fit cover

      .detail
        flex 1

        .item
          display flex
          justify-content space-between
          color black

          .title
            flex 1
            font-weight bold
            font-size 1.6rem
            display -webkit-box
            -webkit-box-orient vertical
            -webkit-line-clamp 1
            overflow hidden

          .price
            font-size 1.8rem
            margin-left .5rem

            .icon-currency
              font-size 1.2rem

          &.spec-num
            color #999
            font-size 1.5rem
            margin 1rem 0

          &.time-totalPrice
            display flex
            justify-content space-between
            align-items baseline

          .time
            font-size 1.2rem
            color #515a6e

          .totalPrice
            font-size 1.7rem
            color black

            .icon-currency
              font-size 1.2rem

    .foot
      margin .8rem 0
      text-align right

      /deep/ .van-button
        font-size 1.4rem
        height 3.2rem
        padding 0 1.5rem
        margin-left 1rem

</style>