<template>
  <div class="m-group">
    <Search :readonly="true" :autofocus="true" :placeholder="'输入名称搜索'" @click.native="doFocus" />

    <van-tree-select :style="{ height : curHeight }" :items="items" :main-active-index.sync="active" @click-nav="clickNav">
      <template #content>
        <div class="empty" v-if="items[active].list.length == 0">
          <el-empty :image-size="100" description="暂无相关商品"></el-empty>
        </div>

        <div class="cate-list">
          <div class="list" v-for="(item, index) in items[active].list" :key="index" @click="$router.push(`/m/detail/${ item.id }`)">
            <div class="img"><img :src="item.thumbnail" alt=""></div>
            <div class="title">{{ item.title }}</div>
          </div>

          <i></i>
          <i></i>
          <i></i>
        </div>
      </template>
    </van-tree-select>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import Search from "@/views/mobile/components/Search"

export default {
  name: "MGroup",
  components: {
    Search,
  },
  computed: {
    ...mapState(['goodsList']),
  },
  data () {
    return {
      curHeight: 0, // 分类页高度
      active: 0,
      items: [
        { id: 1, index: 0, text: '热门推荐', list: [] },
        { id: 2, index: 1, text: 'iTunes', list: [] },
        { id: 3, index: 2, text: 'Google', list: [] },
        { id: 4, index: 3, text: 'Amazon', list: [] },
        { id: 5, index: 4, text: 'Nintendo', list: [] },
        { id: 6, index: 5, text: 'Steam', list: [] },
      ],
    }
  },

  methods: {
    ...mapActions(['getGoodsList']),

    // 获取页面高度并动态给分类计算高度
    getScreenHeight () {
      let screenH = document.documentElement.offsetHeight
      this.curHeight = Number(screenH - 40) + 'px'
    },

    // 绑定 focus 函数
    doFocus () {
      this.$router.push('/m/search')
    },

    // 默认初始化热门推荐列表
    getCategoriesData () {
      this.getGoodsList()

      this.items.map(i => {
        // 模拟推荐数据
        if (i.index == 0) {
          i.list = this.goodsList.filter(j => j.id > 10)
        }
      })
    },

    // 点击左侧标签获取相应的数据
    clickNav (index) {
      console.log(index)

      this.getGoodsList()
      this.items.map(i => {
        // 模拟当前分类下的数据
        if (i.index == index) {
          i.list = this.goodsList.filter(k => k.title.includes(i.text))
        }
      })
    },

  },

  mounted() {
    this.getScreenHeight()
    this.getCategoriesData()
  }

}
</script>

<style scoped lang="stylus">
.m-group
  height calc(100% - 4rem)

  /deep/ .van-tree-select
    height 100%

    .van-tree-select__content
      flex 3

      .van-sidebar-item__text
        display flex
        justify-content center

  .cate-list
    padding 0 1rem
    display flex
    flex-wrap wrap
    justify-content space-between

    .list, i
      width 33.33%
      margin-bottom 1.5rem

    i
      width auto
      margin-bottom 0

    .list
      .img
        border-radius 5px
        padding 1rem

        img
          width 100%
          height 100%
          object-fit cover

      .title
        display -webkit-box
        -webkit-box-orient vertical
        -webkit-line-clamp 1
        overflow hidden
        padding 0 1rem
        font-size 1.2rem
        color #666
        font-weight lighter

</style>