<template>
  <div class="search-box main">
    <loadingComp :loading="loading" v-if="loading"></loadingComp>

    <div class="empty" v-if="list.length == 0">
      <el-empty :image-size="100" description="没有相关商品哦"></el-empty>
    </div>

    <div class="animate__animated animate__fadeIn list">
      <ul>
        <li v-for="(item, index) in list" :key="index">
          <div class="li-box" @click="toDetail(item.id)">
            <img class="thumbnail" :src="item.thumbnail" alt="">

            <div class="info">
              <div class="info-title">
                {{ item.title }}
              </div>

              <span class="price-box">￥ <span class="price">{{ item.spec.filter(i => i.active)[0].price | twoDecimal }}</span></span>
            </div>

            <div class="intro">
              <span class="item zhengPin">官方正品</span>
              <span class="item faHuo">自动发货</span>
              <span class="item volume">销量{{ item.sold }}</span>
            </div>
          </div>
        </li>

        <i></i>
        <i></i>
        <i></i>
        <i></i>
        <i></i>
        <i></i>
      </ul>
    </div>

    <el-pagination class="pagination" background layout="prev, pager, next" :total="9" v-if="list.length > 0"></el-pagination>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'

export default {
  name: "SearchView",
  data () {
    return {
      loading: false,
      timer: null,
      list: [],
    }
  },

  computed: {
    ...mapState(['wd', 'group', 'goodsList'])
  },

  methods: {
    ...mapActions(['getGoodsList', 'setGroup']),

    // 初始化列表
    getCurrentList () {
      this.loading = true

      this.timer = setTimeout(() => {
        this.setGroup(this.$route.query.group)
        this.getGoodsList()
        this.list = this.goodsList.filter(i => i.group == this.group)

        this.loading = false
      }, 500)
    },

    // 关键词搜索列表
    getWdList () {
      if (this.wd) {
        this.loading = true

        this.timer = setTimeout(() => {
          this.getGoodsList()
          this.list = this.goodsList.filter(i => i.title.includes(this.wd))

          this.loading = false
        }, 500)
      }
    },

    toDetail (id) {
      this.$router.push(`/detail/${ id }`)
    },
  },

  mounted () {
    this.getCurrentList()
    this.getWdList()
  },

  destroyed() {
    clearTimeout(this.timer)
  }
}
</script>

<style scoped lang="stylus">
.search-box
  margin-top 1rem
  margin-bottom 3rem

  .list
    width 100%

    ul
      display flex
      justify-content space-between
      align-items center
      flex-wrap wrap

      li, i
        width 19.25%
        background-color white
        margin-bottom 1.5rem
        border-radius 2px
        padding-top 5px

      i
        margin-bottom 0
        padding-top 0

      li
        height 30.8rem
        border 1px solid #ddd
        overflow hidden

        .li-box
          display flex
          flex-direction column

          .thumbnail
            width calc(100% - 1rem)
            height 22.5rem
            object-fit cover
            margin 0 5px
            cursor pointer

          .info
            display flex
            margin .5rem .5rem 0

            .info-title
              width 70%
              color #515a6e
              height 4.2rem
              display -webkit-box
              -webkit-box-orient vertical
              -webkit-line-clamp 2
              overflow hidden

            .price-box
              color red
              display flex
              align-items flex-end

              .price
                font-size 2.2rem
                font-weight bold
                margin-left .5rem

          .intro
            color white
            margin 2%
            display flex
            align-items center

            .item
              height 2.2rem
              line-height 2.2rem
              margin 2px 4px 2px 0
              padding 0 8px
              font-size 1.2rem
              border-radius 3px
              border 1px solid transparent

              &.zhengPin
                background-color rgba(81, 155, 224, 1)
                border 1px solid rgba(81, 155, 224, 1)

              &.faHuo
                background-color rgba(107, 224, 81, 1)
                border-color rgba(107, 224, 81, 1)

              &.volume
                color #13c2c2
                background-color #e6fffb
                border-color #87e8de
                margin-left auto

.pagination
  display flex
  justify-content center
  margin 1rem 0


</style>