import Vue from 'vue'
import VueRouter from 'vue-router'
import Layout from '@/views/Layout.vue'
import HomeView from '@/views/HomeView.vue'
import SearchView from "@/views/SearchView"
import QueryView from '@/views/QueryView.vue'
import DetailView from "@/views/DetailView"
import NotFound from "@/views/NotFound"

// mobile
import MLayout from '@/views/mobile/Layout'
import MHome from '@/views/mobile/HomeView'
import MQuery from '@/views/mobile/QueryView'
import MGroup from '@/views/mobile/GroupView'
import MSearch from '@/views/mobile/SearchView'
import MDetail from '@/views/mobile/DetailView'
import MPay from '@/views/mobile/Pay'

Vue.use(VueRouter)

// pc 端路由
const pcRoutes = [
  {
    path: '/',
    component: Layout,
    children: [
      {
        path: '/',
        // component: () => import(/* webpackChunkName: "search" */ '../views/SearchView.vue')
        component: HomeView,
      },
      {
        path: 'search',
        component: SearchView,
      },
      {
        path: 'query',
        component: QueryView
      },
      {
        path: 'detail/:id',
        component: DetailView,
      },
    ],
  },
  {
    path: '*',
    component: NotFound,
  }
]

// mobile 端路由
const mobileRoutes = [
  {
    path: '/',
    redirect: '/m/'
  },
  {
    path: '/m',
    component: MLayout,
    children: [
      {
        path: '',
        component: MHome,
        meta: {
          isShowTabbar: true,
          isShowContactItem: true,
        },
      },
      {
        path: 'query',
        component: MQuery,
        meta: {
          isShowTabbar: true,
          isShowContactItem: true,
        },
      },
      {
        path: 'group',
        component: MGroup,
        meta: {
          isShowTabbar: true,
          isShowContactItem: true,
        },
      },
      {
        path: 'search',
        component: MSearch,
        meta: {
          isShowHomeItem: true,
          isShowSearchItem: true,
          isShowContactItem: true,
        }
      },
      {
        path: 'detail/:id',
        component: MDetail,
      },
      {
        path: 'pay',
        component: MPay
      },
    ]
  },
  {
    path: '*',
    component: NotFound,
  }
]

// 检测是否移动端
let isMobile = /(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i.test(navigator.userAgent)

const router = new VueRouter({
  mode: 'history', // 打包上线后需要后台支持
  base: process.env.BASE_URL,
  routes: isMobile ? mobileRoutes : pcRoutes
})

