<template>
  <div class="midbar">
    <div class="logo"><a href="/"><img src="@/assets/logo.png" alt=""></a></div>

    <div class="search-box">
      <div class="search">
        <input type="text" class="search-text" v-model="keyword" @input="changeWd(keyword)" @keyup.enter="searchWd(keyword)" placeholder="输入关键词搜索">
        <button class="search-btn" @click="searchWd(keyword)">搜索</button>
      </div>

      <ul class="search-recommend-list">
        <li v-for="(item, index) in searchRecommendList" :key="index">
          <router-link class="li-a" :to="'/detail/' + item.id">{{ item.name }}</router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'

export default {
  name: "MidBar",
  data () {
    return {
      keyword: '',
      searchRecommendList: [],
    }
  },
  computed: {
    ...mapState(['wd', 'goodsList'])
  },
  methods: {
    ...mapActions(['setWd']),

    // 获取搜索推荐列表
    getSearchRecommendList () {
      let searchRecommendList = [
        { id: 1, name: '美国-Apple iTunes 苹果礼品卡' },
        { id: 2, name: '加拿大-Apple 苹果卡' },
        { id: 3, name: '美国-Google play 谷歌礼品卡' },
      ]

      this.searchRecommendList = searchRecommendList
    },

    // 监听搜索框内容变化
    changeWd (value) {
      let key = this.trim(value)
      this.setWd(key)
      sessionStorage.setItem('keyword', key)
      // this.$router.push(`/search?wd=${ this.wd }`)
    },

    // 搜索商品
    searchWd (value) {
      let key = this.trim(value)
      this.keyword = value
      this.setWd(key)
      this.$router.push(`/search?wd=${ key }`)
    },

  },

  mounted() {
    this.keyword = sessionStorage.getItem('keyword')
    this.getSearchRecommendList()
  }
}
</script>

<style scoped lang="stylus">
.midbar
  display flex
  justify-content space-between
  align-items center

  .logo
    width 30rem
    height 13rem
    display flex
    align-items center

    img
      width 100%

  .search-box
    display flex
    flex-direction column

    .search
      width 62.5rem
      height 4rem
      border 2px solid rgba(15, 14, 16, 1)
      display flex
      justify-content space-between
      align-items center
      border-radius 2px

      .search-text
        width 80%
        height 3.5rem
        padding 0 1rem
        font-size 1.5rem

      .search-btn
        width 20%
        height 100%
        color white
        font-size 1.5rem
        background-color rgba(15, 14, 16, 1)
        cursor pointer

    .search-recommend-list
      margin 1rem 0

      li
        padding 0 1.2rem
        display inline-block
        line-height 1.1
        cursor pointer

        a
          color #888

        &:first-child
          padding-left 0

        &:nth-child(2)
          border 1px solid #ccc
          border-top transparent
          border-bottom transparent

        &:last-child
          padding-right 0
</style>