<template>
  <div class="search-box">
    <van-search class="search-main" :isSlot="_slot" :readonly="_readonly" :background="_background" :shape="_shape" :value="_searchValue" :autofocus="_autofocus" :placeholder="placeholder" @input="change" @search="_doSearch(_searchValue)" ref="vanSearch" />
    <slot v-if="_slot"></slot>
    <div v-if="!_slot">
      <div class="search-btn" @click="_doSearch(_searchValue)" v-if="_rightText">{{ _rightText }}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "SearchView",
  props: ['isSlot', 'readonly', 'background', 'shape', 'searchValue', 'autofocus', 'placeholder', 'doSearch', 'rightText'],
  data () {
    return {
    }
  },

  computed: {
    _slot () {
      return this.isSlot || false
    },
    _readonly () {
      return this.readonly || false
    },
    _background () {
      return this.background || ''
    },
    _shape () {
      return this.shape || 'round'
    },
    _autofocus () {
      return this.autofocus
    },
    _searchValue () {
      return this.searchValue || ''
    },
    _rightText () {
      return this.rightText || null
    },
  },

  methods: {
    _default () {},

    // 如果父组件传入 自动聚焦 为 true 则自动聚焦
    doAutofocus () {
      this.autofocus ? this.$refs.vanSearch.querySelector('input').focus() : this._default()
    },

    _doSearch () {
      return this.doSearch() || this._default()
    },

    change (value) {
      this.$emit('update:searchValue', value)
    },
  },

  mounted () {
    this.doAutofocus()
  }

}
</script>

<style scoped lang="stylus">
.search-box
  width 100%
  display flex
  align-items center

  .search-main
    flex 1

  .search-btn
    font-size 1.4rem
    line-height 5.4rem
    background-color white
    padding-right 1rem
</style>