/*
* 直接更新 state 的多个方法的对象
* */
import {
    SETGROUP,
    GETGOODSLIST,
    SETWD,
    GETDETAILPAGEDATA,
    GETMOBILEDETAILPAGEDATA,
} from './mutation-types'

export default {
    // 设置 group
    [SETGROUP] (state, { group }) {
        state.group = parseInt(group)
    },

    // 初始化商品列表
    [GETGOODSLIST] (state, { goodsList }) {
        state.goodsList = goodsList
    },

    // 设置关键词
    [SETWD] (state, { key }) {
        state.wd = key
    },

    // 获取详情页数据
    [GETDETAILPAGEDATA] (state, { res }) {
        state.detailPageData = res
    },

    // 获取移动端详情页数据
    [GETMOBILEDETAILPAGEDATA] (state, { res }) {
        state.mobileDetailPageData = res
    },

}