<template>
  <div class="m-pay">
    <div class="pay-head">
      <div class="title"><i class="van-icon van-icon-balance-pay"></i> 收银台</div>
      <div class="money"><em>￥</em> {{ finalPrice | twoDecimal }}</div>
    </div>

    <div class="pay-info">
      <van-form validate-first>
        <!-- 通过 pattern 进行正则校验 -->
        <van-field
            v-model="phoneNumber"
            name="pattern"
            label="手机号"
            label-width="6rem"
            placeholder="必填，作为购买凭证"
            :rules="[{ pattern, message: '请输入正确的手机号' }]"
        />

        <van-field name="checkbox" label="附加服务" label-width="6rem">
          <template #input>
            <van-checkbox v-model="additionalService" shape="square">发送到我邮箱(免费)</van-checkbox>
          </template>
        </van-field>

        <!-- 通过 validator 进行函数校验 -->
        <van-field
            v-model="email"
            name="validator"
            label="邮箱"
            label-width="6rem"
            placeholder="必填，作为购买凭证"
            :rules="[{ validator, message: '请输入正确的邮箱' }]"
            v-if="additionalService"
        />

        <van-field
            v-model="remarks"
            rows="2"
            autosize
            label="订单备注"
            label-width="6rem"
            type="textarea"
            maxlength="100"
            placeholder="选填，请先和客服协商一致"
            show-word-limit
        />
      </van-form>
    </div>

    <div class="alert-info">
      提示：付款后若未自动跳转，请前往首页->订单查询，即可查询所购商品内容
    </div>

    <div class="pay-footer">
      <van-button round type="info" @click="goToAlipay"><i class="iconfont icon-zhifubao"></i> 支付宝支付</van-button>
      <van-button round disabled type="primary"><i class="iconfont icon-weixin1"></i> 暂时维护</van-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "PayView",
  data () {
    return {
      info: {}, // 购买的商品信息
      phoneNumber: '',
      additionalService: false,
      email: '',
      remarks: '',
      pattern: /^(0|86|17951)?(13[0-9]|15[012356789]|166|17[3678]|18[0-9]|14[57])[0-9]{8}$/,
    }
  },

  computed: {
    finalPrice () {
      return (this.info.selectedSkuComb?.price / 100) * this.info?.selectedNum
    },
  },

  methods: {
    // 校验函数返回 true 表示校验通过，false 表示不通过
    validator(val) {
      return /\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*/.test(val)
    },

    // 获取商品购买信息
    getGoodInfo () {
      this.info = JSON.parse(this.$route.query.info)
    },

    // 支付宝支付配置
    goToAlipay () {
      // 校验手机号
      if (!this.pattern.test(this.phoneNumber)) {
        this.$toast('请填写正确的手机号')
        return
      }

      // 校验邮箱
      if (this.additionalService && !this.validator(this.email)) {
        this.$toast('请填写正确的邮箱')
        return
      }

      // 支付宝配置
      console.log(this.info)
    },

  },

  mounted() {
    // 加载商品信息
    this.getGoodInfo()
  }
}
</script>

<style scoped lang="stylus">
.m-pay
  min-height 100vh
  background-color #f5f6f7
  overflow hidden

  .pay-head
    background-color white
    text-align center
    margin 1rem
    border-radius 1rem 1rem 0 0
    position relative

    &:before
      content ''
      position absolute
      left 0
      right 0
      bottom 0
      height 2px
      background repeating-linear-gradient(-45deg,#ff6c6c,#ff6c6c 20%,transparent 0,transparent 25%,#1989fa 0,#1989fa 45%,transparent 0,transparent 50%)
      background-size 8rem

    .title
      height 4rem
      line-height 4rem
      font-size 1.6rem
      border-bottom 1px solid #e8eaec

      i
        font-size 1.8rem
        margin-right .1rem
        vertical-align middle

    .money
      line-height 10rem
      font-size 3.5rem
      color red

      em
        font-size 2rem
        font-style normal
        margin-right -1rem

  .pay-info
    margin 0 1rem
    border-radius 1rem
    background-color white
    overflow hidden

  .alert-info
    margin 1rem
    background-color #f0faff
    border 1px solid #fff
    padding .8rem
    border-radius 1rem
    line-height 1.6rem

  .pay-footer
    position fixed
    bottom 0
    left 0
    padding .8rem
    display flex
    justify-content space-between
    width calc(100% - 1.6rem)

    .van-button
      width 48%
      height 4.4rem

      .van-button__text
        display flex
        align-items center

        i
          margin-right .5rem

</style>