<template>
  <div class="navbar">
    <div class="main navbar-box">
      <ul>
        <li v-for="(item, index) in navList" :key="index">
          <router-link :to="item.to" exact-active-class="active">{{ item.name }}</router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'

export default {
  name: "NavBar",
  data () {
    return {
      navList: [],
    }
  },

  computed: {
    ...mapState(['group'])
  },

  methods: {
    ...mapActions(['setGroup']),

    // 异步获取导航栏
    getNavList () {
      let navList = [
        { name: '全部', to: '/' },
        { name: '热门推荐', to: '/search?group=80' },
        { name: 'iTunes', to: '/search?group=81' },
        { name: 'Google', to: '/search?group=82' },
        { name: 'Amazon', to: '/search?group=83' },
        { name: 'Nintendo', to: '/search?group=84' },
        { name: 'Steam', to: '/search?group=85' },
      ]

      this.navList = navList
    },
  },

  mounted() {
    // 初始化导航列表
    this.getNavList()
  }
}
</script>

<style scoped lang="stylus">
.navbar
  width 100%
  height 4rem
  background-color rgba(15, 14, 16, 1)

  .navbar-box
    ul
      display flex
      align-items center
      height 4rem

      li
        height 100%
        display flex
        align-items center

        a
          color white
          font-size 1.6rem
          display: block;
          padding: 0 10px;
          font-weight: 700;
          line-height 4rem

          &.active, &:hover
            background-color #06f
            border-bottom-color #06f

</style>