<template>
  <div class="search-box main">
    <loadingComp :loading="loading" v-if="loading"></loadingComp>

    <div class="list" id="homeList" style="min-height: 66rem">
      <ul>
        <li v-for="(item, index) in goodsList" :key="index">
          <a class="animate__animated animate__fadeIn li-a" @click.prevent="$router.push(`/detail/${ item.id }`)">
            <img class="thumbnail" :src="item.thumbnail" alt="">

            <div class="info">
              <div class="info-title">
                <span class="rec" v-if="item.isRecommend">推荐</span>
                {{ item.title }}
              </div>

              <span class="price-box">￥ <span class="price">{{ item.spec.filter(i => i.active)[0].price | twoDecimal }}</span></span>
            </div>
          </a>
        </li>

        <i></i>
        <i></i>
        <i></i>
        <i></i>
        <i></i>
        <i></i>
      </ul>
    </div>

    <el-pagination class="pagination" background layout="prev, pager, next" :total="9"></el-pagination>
  </div>
</template>

<script>
// import axios from 'axios'
import { mapState, mapActions } from 'vuex'

export default {
  name: "SearchView",
  props: ['id'],
  data () {
    return {
      loading: false,
      timer: null,
    }
  },

  computed: {
    ...mapState(['group', 'goodsList']),
  },

  methods: {
    ...mapActions(['getGoodsList']),

    // 加载效果
    loadStatus () {
      // 获取首页商品列表
      this.loading = true

      this.timer = setTimeout(() => {
        // 获取首页商品列表
        this.getGoodsList()

        this.loading = false
      }, 500)
    },

  },

  mounted () {
    this.loadStatus()
  },

  destroyed() {
    clearTimeout(this.timer)
  }
}
</script>

<style scoped lang="stylus">
.search-box
  margin-top 1rem
  margin-bottom 3rem

  .list
    width 100%

    ul
      display flex
      justify-content space-between
      align-items center
      flex-wrap wrap

      li, i
        width 19.25%
        background-color white
        margin-bottom 1.5rem
        border-radius 2px
        padding-top 5px

      i
        margin-bottom 0
        padding-top 0

      li
        height 30.8rem
        overflow hidden
        border 1px solid transparent

        .li-a
          display flex
          flex-direction column

          .thumbnail
            width calc(100% - 1rem)
            height 22.5rem
            object-fit cover
            margin 0 5px
            cursor pointer

          .info
            margin 1rem 1rem 0

            .info-title
              color #515a6e
              height 4.2rem
              display -webkit-box
              -webkit-box-orient vertical
              -webkit-line-clamp 2
              overflow hidden

              .rec
                color white
                background-color #ee0a24
                border-radius 99rem
                padding 1px 4px
                font-size 1.2rem
                line-height 1.6rem

            .price-box
              color red
              display flex
              align-items flex-end

              .price
                font-size 1.8rem
                font-weight bold
                margin-left .5rem

.pagination
  display flex
  justify-content center
  margin 1rem 0


</style>