/*
* 格式化字符串前后空格
* */

/*
* 方法一:
*   返回函数后,到 main.js 中引入
*       import fun from '@/utils/trim'
*   在 Vue 的原型上添加方法:
*       Vue.prototype.fun = fun
*   调用
*       this.fun(value)
* */
// export default (value) => value.toString().replace(/(^\s+)|(\s+$)/gm, '')

/*
* 方法二:
*   可能会报错: exports is not defined
* */
/*exports.install = (Vue) => {
    Vue.prototype.trim = (value) => value.toString().replace(/(^\s+)|(\s+$)/gm, '')
}*/

/*
* 方法三: (建议)
*   直接将函数定义在 Vue 的原型上并返回,在 main.js 中引入后使用 Vue.use(xx),再到页面上用 this.xx(value) 使用即可
* */
export default {
    install (Vue) {
        Vue.prototype.trim = (value) => value.toString().replace(/(^\s+)|(\s+$)/gm, '')
    }
}


